   
.masthead {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  height: 100vh; /* if you don't want it to take up the full screen, reduce this number */
  background-size: cover !important;
  background: radial-gradient(ellipse at center, rgba(0,0,0,0) 0%, rgba(0,0,0,.2) 2%, rgba(0,0,0,0.65) 100%),
   url("../../images/tech4.jpg") no-repeat center center scroll;   
}

.Hero-header {
  font-style: normal;
  font-weight: bold;
  color: #eee;
  font-size: 11vmin;
  letter-spacing: 0.03em;
  line-height: 1;
  text-shadow: 1px 2px 4px rgba(0, 0, 0, 0.8);
  margin-bottom: 51px;
}
    
      
.Hero-button {
  background: #04b404;
  border-radius: 50px;
  display: inline-block;
  border: none;
  padding: 0.75rem 1.5rem;
  margin: 0;
  text-decoration: none;
  color: #ffffff;
  font-size: 1.2rem;
  cursor: pointer;
  text-align: center;
}
.Hero-button:hover {
  background: #1d721f
}
.Hero-button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}
